@keyframes slideDown {
  from {
    transform: translateY(-100px); /* A partire da 100px sopra la posizione iniziale */
    opacity: 0; /* Opzionale: parte da completamente trasparente */
  }
  to {
    transform: translateY(0);
    opacity: 1; /* Opzionale: diventa completamente opaco */
  }
}
@keyframes slideUp {
  from {
    transform: translateY(100px); /* Inizia da 100px sotto la posizione iniziale */
    opacity: 0; /* Parte da completamente trasparente */
  }
  to {
    transform: translateY(0);
    opacity: 1; /* Diventa completamente opaco */
  }
}

.h1-slide-up, .h7-slide-up {
  animation: slideUp 1.8s ease forwards; /* Durata di 0.5 secondi */
}
.svg-slide-down {
  animation: slideDown 1.8s ease forwards; /* Durata di 1 secondo, "ease" per un effetto di smorzamento, "forwards" per mantenere lo stato finale */
}

/* ---------------------------- */

.banner-container {
  position: relative;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.contact-button {
  font-family: ui-sans-serif;
  position: absolute;
  top: 30px;
  right: 30px;
  background-color: whitesmoke;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: whitesmoke;
  font-family: "Georgia, serif";
}

.logo {
  width: 150px;
  height: auto;
}

h1 {
  margin: 20px 0;
  font-size: 7em;
}

h7 {
  font-size: 2em;
  margin-top: -10px; /* Adjust according to your design */
}

@media (max-width: 1331px) {
  .logo {
    width: 100px;
  }
  
  h1 {
    font-size: 2em;
  }
  
  h7 {
    font-size: 1em;
  }
}
