.App {
  /*  min-height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: black;
  overflow: hidden;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

body::-webkit-scrollbar {
  width: 0;
  /* Nasconde la barra di scorrimento su browser Webkit come Chrome e Safari */
}

body {
  -ms-overflow-style: none;
  /* Nasconde la barra di scorrimento su browser basati su IE e Edge */
}
