.squareContainer {
  
}

.square {
    padding: 1.3em;
    margin-top: 3em;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.square img {
    max-width: 100%;
    height: auto;
}

.contactButton {
    grid-column: span 6;
    background-color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    width: 20em;
    height: 4em;
    font-family: ui-sans-serif;
    padding: 10px 20px;
    text-align: center;
    margin-inline: auto;
    margin-top: 4em;
}

/* Responsività per smartphone */
@media (max-width: 1331px) {
    .squareContainer {
        grid-template-columns: repeat(3, 1fr);
        /* 3 colonne per schermi piccoli */
        margin: 0 auto 5em;
        margin-top: 3em;
    }

    .contactButton {
        grid-column: span 3;
    }
}

/* ---------------------- */
.Pcontainer {
    height: 76em;
    background-image: url('../../assets/partnerback.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Ptitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}

.point {
    font-size: 6em
}

.PsectionTitle {
    width: 80em;
    margin-inline: auto;
}

.Psubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.Ptext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: left;
    font-size: 2em;
}

@media (max-width: 1331px) {
    .Pcontainer {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 20px;
    }

    .Ptext {


        font-size: 1.4em;
    }

    .PsectionTitle {
        width: 21.8em;
    }

    .point {
        font-size: 4em
    }

    .Ptitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Psubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.7em;
        width: auto;
    }
}

/* for services */
.Pservices-outer-container {
    background-color: #252b2c;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}