.Fcontainer {
    height: 63em;
    background-image: url('../../../assets/finacialservices.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Ftitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}

.point {
    font-size: 6em
}

.FsectionTitle {
    width: 80em;
    margin-inline: auto;
}

.Fsubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.Ftext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: left;
    font-size: 2em;
}

@media (max-width: 1331px) {
    .Fcontainer {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 20px;
    }

    .Ftext {


        font-size: 1.4em;
    }

    .FsectionTitle {
        width: 21.8em;
    }

    .point {
        font-size: 4em
    }

    .Ftitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Fsubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.7em;
        width: auto;
    }
}

/* for services */
.Fservices-outer-container {
    background-color: #131212;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.Fservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    width: 900px;
    margin: auto;
    color: white;
}

.Fservice {
    margin-bottom: 30px;
}

.Fservice h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.Fservice p {
    font-size: 16px;
    line-height: 1.F5;
}

@media (max-width: 1331px) {
    .Fservices-container {
        padding: 10px;
        width: auto;
    }

    .Fservice h2 {
        font-size: 20px;
    }

    .Fservice p {
        font-size: 14px;
    }
}

/* for the image */
.Ftecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 1331px) {
    .Ftecimage {
        width: 101%;
    }
    .Fteam-intro {
       flex-direction: column;
    }
    
}

/* for the button */
.Fcontact {
    font-family: ui-sans-serif;
    display: flex;
    margin-inline: auto;
    background-color: whitesmoke;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}

/* -------------------------------------------- */
.Fteam-intro {
    display: flex;
    justify-content: center;
    align-items: center; 
    margin-bottom: 5em;
    width: 91%;
    margin-inline: auto;
}
@media (min-width: 768px) {
    .FimageTextSection {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 50px 0;
    }

    .team-intro-textnew {
        max-width: 90%;
        margin-left: 0;
        margin-top: 20px;
    }

    .FimageTextSection img {
        max-width: 50%;
        margin-right: 20px;
    }

    .FimageTextSection p {
        max-width: 45%;
    }
}

.team-intro-textnew {
    max-width: 80%;
    text-align: left;
    font-size: 1.3em;
    margin-left: 8em;
    width: 30em;
    margin-right: 8em;
    margin-bottom: 1em;
}

hr {
    border: none;
    height: 2px;
    background-color: #333;
    margin: 20px 0;
    margin-inline: auto;
    width: 80%;
}

.newh1TitleSection {
    margin-inline: auto;
    display: flex;
    justify-content: space-evenly;
    font-size: 4em;
}

.newh2TitleSection {
    margin-inline: auto;
    display: flex;
    justify-content: space-evenly;

}

.pointnew {
    margin-inline: auto;
    display: flex;
    justify-content: space-evenly;
    margin-top: -1em;
    margin-bottom: 1.5em;
    color: red;
    font-size: 4em;

}

/* Per smartphone e dispositivi più piccoli */
@media (max-width: 767px) {
    .FimageTextSection {
        display: block;
        padding: 20px 0;
    }

    .FimageTextSection img {
        width: 100%;
        margin: 0;
        margin-bottom: 20px;
        color: whitesmoke;
    }

    .newh1TitleSection {
        font-size: 2.5em;
    }

    .newh2TitleSection {
        font-size: 1.5em;
    }

    .pointnew {
        font-size: 2em;
    }

    .FimageTextSection p {
        width: 100%;
        color: whitesmoke;
    }
}

/* -------------------------3rd section-------------------------*/

.pricing-section {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    background-color: #131212;
    padding: 20px;
}

.pricing-card {
    flex: 1;
    max-width: 300px;
    border: 1px solid white;
    padding: 20px;
    margin: 0 5em;
    color: white;
}

.textPricing {
    color: whitesmoke;
    display: flex;
    margin-inline: auto;
    justify-content: space-evenly;
    font-size: 4em;
    padding-top: 1em;
}

.pricing-card h2 {
    text-align: center;
    margin-top: 0;
}

.pricing-card p {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin: 20px 0;
}

.pricing-card ul {
    list-style: none;
    padding: 0;
}

.pricing-card li {
    padding: 10px 0;
    text-align: center;
}

.pricing-card button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: white;
    color: #131212;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

/* Responsiveness for mobile */
@media (max-width: 1331px) {
    .pricing-section {
        display: flex;
        flex-wrap: wrap;
    }

    .textPricing {
        padding-top: 1em;
        font-size: 3em;
    }

    .pricing-section::after {
        content: "";
        display: table;
        clear: both;
    }

    .pricing-card {
        margin-bottom: 20px;
        margin: 1em 10px;
        max-width: 100%;
        /* Modificato per occupare l'intera larghezza dello schermo in modalità mobile */
    }
}