.Lcontainer {

    background-image: url('../../../assets/luxury2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Ltitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}

.point {
    font-size: 6em
}

.LsectionTitle {
    width: 80em;
    margin-inline: auto;
}

.Lsubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.Ltext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: center;
    font-size: 2em;
}

@media (max-width: 1331px) {
    .Lcontainer {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 20px;
    }

    .Ltext {
        font-size: 1.4em;
    }

    .LsectionTitle {
        width: 21.8em;
    }

    .point {
        font-size: 4em
    }

    .Ltitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Lsubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.5em;
        width: auto;
    }
}

/* for services */
.Lservices-outer-container {
    background-color: #5b5b59;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.Lservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    width: 900px;
    margin: auto;
    color: white;
}

.Lservice {
    margin-bottom: 30px;
}

.Lservice h2 {
    font-size: 40px;
    margin-bottom: 10px;
}

.Lservice p {
    font-size: 30px;
    line-height: 1.5;
}

@media (max-width: 1331px) {
    .Lservices-container {
        padding: 10px;
        width: auto;
    }

    .Lservice h2 {
        font-size: 20px;
    }

    .Lservice p {
        text-align: left;
        font-size: 14px;
    }
}

/* for the image */
.Ltecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 1331px) {
    .Ltecimage {
        width: 101%;
    }
}

/* for the button */
.Lcontact {
    text-align: center;
    width: 20em;
    height: 4em;
    font-family: ui-sans-serif;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}