.container {

    background-image: url('../../../assets/technology.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.title {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;

}

.sectionTitle {
    width: 80em;
    margin-inline: auto;
}
.subtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.text {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: left;
    font-size: 2em;
}

@media (max-width: 1331px) {
    .container {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .text {
        font-size: 1.4em;
    }
    .sectionTitle {
        width: 21.8em;
    }
    .title {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .subtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.5em;
        width: auto;
    }
}

/* for services */
.services-outer-container {
    background-color: #1F2A44;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.services-container {
    padding: 20px;
    font-family: ui-sans-serif;
   /*  max-width: 800px; */
    margin: auto;
    color: white;
    width: 900px;
}

.service {
    margin-bottom: 30px;
}

.service h2 {
    font-size: 40px;
    margin-bottom: 10px;
}

.service p {
    font-size: 30px;
    line-height: 1.5;
}

@media (max-width: 1331px) {
    .services-container {
        padding: 10px;
        max-width: 800px;
        width: auto;
    }

    .service h2 {
        font-size: 20px;
    }

    .service p {
        text-align: left;
        font-size: 14px;
    }
}

/* for the image */
.tecimage {
    width: 100%;
    margin-left: -4px;
}

@media (max-width: 1331px) {
    .tecimage {
        width: 100%;
    }
}

/* for the button */
.contact {
    text-align: center;
    width: 20em;
    height: 4em;
    font-family: ui-sans-serif;

    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}