.squareContainer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  /* 6 colonne per schermi grandi */
  gap: 10px;
  width: 100%;
  max-width: 1200px;
  margin: 7em auto 7em;
}

.square {
  padding: 1.3em;
  margin-top: 3em;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.square img {
  max-width: 100%;
  height: auto;
}

.contactButton {
  grid-column: span 6;
  background-color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  width: 20em;
  height: 4em;
  font-family: ui-sans-serif;
  padding: 10px 20px;
  text-align: center;
  margin-inline: auto;
  margin-top: 4em;
}

/* Responsività per smartphone */
@media (max-width: 1331px) {
  .squareContainer {
    grid-template-columns: repeat(3, 1fr);
    /* 3 colonne per schermi piccoli */
    margin: 0 auto 5em;
    margin-top: 3em;
  }

  .contactButton {
    grid-column: span 3;
  }
}

