.Icontainer {
  height: 76em;
  background-image: url("../../assets/partnerback.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 50px;
  text-align: center;
  color: white;
  font-family: ui-sans-serif;
}

.Ititle {
  font-family: "ui-sans-serif";
  font-size: 7em;
  margin-bottom: -0.4em;
}

.point {
  font-size: 6em;
}

.IsectionTitle {
  width: 80em;
  margin-inline: auto;
}

.Isubtitle {
  font-family: "ui-sans-serif";
  font-size: 2.6em;
  margin-top: 20px;
  text-align: center;
  width: 30em;
}

.Itext {
  font-family: "ui-sans-serif";
  margin-top: 20px;
