/* for services */
.Iservices-outer-container {
  background-color: #252b2c;
  /* Imposta il colore di sfondo desiderato */
  width: 100vw;
  overflow-x: hidden;
  /* Evita scrollbar orizzontali indesiderati */
}
/* ---------------------- */

/* Section & Card Styles */
.sectionContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
  padding: 7em -1px;
  background-color: #252b2c;
  height: 70em;
  padding-left: 10em;
  padding-right: 10em;
}

.cardContainer {
  width: 23%; /* Taking into account 3 cards per row with gaps */
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
  padding: 20px;
  background-color: #f1f1f1; /* light gray color, you can adjust */
}

.cardContainer.hoverCard {
  background-color: red; /* Assuming hover cards are red. Adjust as needed */
}


.category h4 {
  margin: 0;
}

.category p {
  font-size: 12px;
}

.category h4 {
  margin: 10px 0;
  font-size: 16px;
}

.contactButton {
  width: 33em;
  padding: 10px;
  font-family: ui-sans-serif;
  border: none;
  cursor: pointer;
  text-align: center;
}

/* Responsive Styles */
@media (max-width: 1331px) {
  /* Adjusting for tablet and mobile */
  .sectionContainer {
    align-items: center;
    height: 144em;
    padding-left: 0;
    padding-right: 0;
  }
  .htwo {
    text-align: center;
  }

  .sectiontextmodal {
    flex-direction: column;
  }
  .date-author {
    padding-bottom: 3em;
  }
  .cardContainer {
    width: 80%; /* Making it full width for smaller screens */
    margin-bottom: 20px;
    margin-inline: auto;
    height: 10em;
  }
  .modal-text {
    margin-bottom: 30px;
    margin-left: auto;
    margin-top: 2em;
    font-size: 25px;
    display: contents;
  }
}

.htwo {
  font-size: 30px; /* Ridimensionato da 45px a 30px */
  margin-inline: 10%;
}
/* modale */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 30px;
  width: 80%;
  max-height: 90%;
  overflow-y: auto;
  position: relative;
/*   box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2); */
}

.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.modal-image {
  width: 80%;
  height: auto;
  margin-inline: 10%;
}
 
.date-author  {
  margin: 15px 0;
  font-size: 19px!important;
  color: grey;
}

.modal-text {
  margin-bottom: 30px;
  margin-left: auto;
  width: 32em;
  font-size: 25px;
}

.modal-footer {
  border-top: 1px solid #e0e0e0;
  padding-top: 15px;
}
.sectiontextmodal {
  display: flex;
  width: 80%;
  margin-inline: auto;
}
