.esgcontainer {

    background-image: url('../../../assets/esg1.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.esgtitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}
.point {
    font-size:6em
}
.esgsectionTitle {
    width: 80em;
    margin-inline: auto;
}
.esgsubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.esgtext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: center;
    font-size: 2em;
}

@media (max-width: 1331px) {
    .esgtext {
       
        font-size: 1.4em;
    }
    .esgcontainer {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .esgsectionTitle {
        width: 21.8em;
    }
    .point {
        font-size:4em
    }
    .esgtitle {
        font-family: 'ui-sans-serif';
        font-size: 3em;
    }

    .esgsubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.5em;
        width: auto;
    }
}

/* for services */
.esgservices-outer-container {
    background-color: #5D7B6B;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.esgservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    margin: auto;
    color: white;
    width: 900px;
}

.esgservice {
    margin-bottom: 30px;
}

.esgservice h2 {
    font-size: 40px;
    margin-bottom: 10px;
}

.esgservice p {
    font-size: 30px;
    line-height: 1.5;
}

@media (max-width: 1331px) {
    .esgservices-container {
        padding: 10px;
        width: auto;
    }

    .esgservice h2 {
        font-size: 20px;
    }

    .esgservice p {
        font-size: 14px;
    }
}

/* for the image */
.esgtecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 1331px) {
    .esgtecimage {
        width: 101%;
    }
}

/* for the button */
.esgcontact {
    text-align: center;
    width: 20em;
    height: 4em;
    font-family: ui-sans-serif;

    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}