.footer-container {
    background-color: black;
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: auto;
  }
  
  .footer-content {
    text-align: left;
    font-size: 2em;
    width: 95vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .footer-service{
    margin-right: 4em;
  }
  .footer-company {
    margin-left: 20px;
  }
  
  .footer-ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-li {
    cursor: pointer;
  }
  
  .footer-copyright {
    margin-top: 20px;
    font-size: 17px;
  }
  .svg {
    width: 180px;
    height: 140px;
 
  }
  .socialIcon{
    display: flex;
    justify-content: space-evenly;
  }
  @media (max-width: 1331px) {
    .footer-content {
      font-size: 1.3em;
      margin-left:-21px;
    }
    .svg {
      width: 5em;
      height: 5em;
    }
    .footer-service{
      margin-right: auto;
      margin-left: 1em;
    }
    .socialIcon{

    }
    .footer-logo{
      margin-left: 3px;
    }
}
